<!-- 初级数字键盘 -->
<template>
	<div class="keyboard-num" ref="keyNum">
		<header>
			<img src="data:image/png;base64," >
			<div class="shop-info">
				<p class="title">家乐福超市</p>
				<p class="intro">
					<span class="icon">商户</span>
					<span class="detail">家乐福超市</span>
				</p>
			</div>
		</header>
		<div class="input-wrap">
			<div class="input-area pixel-1px" @click="showBoard">
				<span>支付金额</span>
				<span class="money">{{money}}<span class='gb'></span>&nbsp;元</span>
			</div>
		</div>
		<div class="logo-wrap">
			<img src="data:image/png;base64,">
		</div>
		<num-board :active.sync=active :value.sync="money"></num-board>
	</div>
</template>
<script>
	import {NumBoard} from '../../components/keyboard'
	export default {
		data(){
			return {
				money:'',
				active:false
			}
		},
		components:{NumBoard},
		methods:{
			showBoard(){
				this.active = true
			},
		},
		mounted(){
			// 此处需设置高度，否则会有位移动画出现
      		this.$refs.keyNum.style.height='calc(100% - 46px)'
		}
	}
</script>
<style lang='scss'>
	@import './keyboard';
	.keyboard-num{
		@include board-wrap;
	}
</style>


